@tailwind base;
@tailwind components;
@tailwind utilities;

/* Hide scrollbars */
.scrollbar-hide {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.scrollbar-hide::-webkit-scrollbar {
  display: none;
}

/* Mobile-first improvements */
@layer base {
  * {
    -webkit-tap-highlight-color: transparent;
  }

  html {
    -webkit-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    text-size-adjust: 100%;
  }

  body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
    background-color: #000000;
    color: #ffffff;
    min-height: 100vh;
    height: 100vh;
    height: 100dvh; /* Dynamic viewport height for mobile */
  }

  #root {
    min-height: 100vh;
    height: 100vh;
    height: 100dvh; /* Dynamic viewport height for mobile */
    display: flex;
    flex-direction: column;
  }

  /* Better touch targets */
  button,
  a,
  input,
  select,
  textarea {
    touch-action: manipulation;
  }

  /* Improved input styling for mobile */
  input,
  select,
  textarea {
    font-size: 16px; /* Prevents zoom on iOS */
  }

  /* Custom scrollbar for webkit browsers */
  ::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }

  ::-webkit-scrollbar-track {
    background: #1a1a1a;
  }

  ::-webkit-scrollbar-thumb {
    background: #4a4a4a;
    border-radius: 4px;
  }

  ::-webkit-scrollbar-thumb:hover {
    background: #6a6a6a;
  }
}

@layer utilities {
  /* Safe area for notched devices */
  .safe-top {
    padding-top: env(safe-area-inset-top);
  }

  .safe-bottom {
    padding-bottom: env(safe-area-inset-bottom);
  }

  .safe-left {
    padding-left: env(safe-area-inset-left);
  }

  .safe-right {
    padding-right: env(safe-area-inset-right);
  }

  /* Touch-friendly utilities */
  .touch-manipulation {
    touch-action: manipulation;
    -webkit-user-select: none;
    user-select: none;
  }

  /* Prevent text selection during touch */
  .no-select {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  /* Modal animations */
  .animate-fadeIn {
    animation: fadeIn 0.2s ease-out;
  }

  .animate-slideUp {
    animation: slideUp 0.3s ease-out;
  }

  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @keyframes slideUp {
    from {
      transform: translateY(100px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }

  /* Star particle background utility */
  .star-particles {
    position: relative;
    overflow: hidden;
  }

  .star-particles::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: 
      radial-gradient(1px 1px at 20px 30px, #ffffff, transparent),
      radial-gradient(1px 1px at 40px 70px, #ffffff, transparent),
      radial-gradient(1px 1px at 90px 40px, #ffffff, transparent),
      radial-gradient(1px 1px at 130px 80px, #ffffff, transparent),
      radial-gradient(1px 1px at 160px 30px, #ffffff, transparent),
      radial-gradient(1.5px 1.5px at 30px 60px, #ffffff, transparent),
      radial-gradient(1.5px 1.5px at 80px 20px, #ffffff, transparent),
      radial-gradient(1.5px 1.5px at 120px 50px, #ffffff, transparent),
      radial-gradient(1.5px 1.5px at 150px 70px, #ffffff, transparent),
      radial-gradient(2px 2px at 50px 90px, #ffffff, transparent),
      radial-gradient(2px 2px at 100px 10px, #ffffff, transparent),
      radial-gradient(2px 2px at 140px 40px, #ffffff, transparent);
    background-repeat: repeat;
    background-size: 200px 100px;
    opacity: 0.7;
    pointer-events: none;
  }

  /* Purple gradient backgrounds */
  .bg-purple-gradient {
    background: linear-gradient(135deg, #7c3aed 0%, #5b21b6 100%);
  }

  .bg-purple-gradient-hover:hover {
    background: linear-gradient(135deg, #8b5cf6 0%, #6d28d9 100%);
  }
}

/* Gilroy and Inter fonts (if available) */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');

.font-gilroy {
  font-family: 'Gilroy', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
}

.font-inter {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
